// TABS

// catalog lang tab

// catalog lang active btn
const catalogLangTabs = document.querySelectorAll(".catalog__lang__item");

for (let i=0; i<catalogLangTabs.length; i++) {
  catalogLangTabs[i].onclick = function() {
    let j = 0;

    while(j < catalogLangTabs.length) {
      catalogLangTabs[j++].className = "catalog__lang__item";
    };

    catalogLangTabs[i].className = "catalog__lang__item is_active";
  };

};

// catalog lang tabs
document.querySelectorAll(".catalog__lang__item").forEach(function(tabsBtn) {
  tabsBtn.addEventListener("click", function(event) {
    const lang = event.currentTarget.dataset.lang

    document.querySelectorAll(".catolog__lang-content__item").forEach(function(tabContent) {
      tabContent.classList.remove('is_active')
    });

    document.querySelector(`[data-lang-target="${lang}"]`).classList.add("is_active")
  });
});

// accordion active btn
const accordionTabs = document.querySelectorAll(".catalog__accordion__art");

for (let i=0; i<accordionTabs.length; i++) {
  accordionTabs[i].onclick = function() {
    let j = 0;

    while(j < accordionTabs.length) {
      accordionTabs[j++].className = "catalog__accordion__art";
    };

    accordionTabs[i].className = "catalog__accordion__art is_active";
  };

};

document.querySelectorAll(".catalog__accordion__art").forEach(function(tabsBtn) {
  tabsBtn.addEventListener("click", function(event) {
    const accordion = event.currentTarget.dataset.accordion

    document.querySelectorAll(".catalog__lang-content__item__left").forEach(function(tabContent) {
      tabContent.classList.remove('is_active')
    });

    document.querySelector(`[data-accordion-target="${accordion}"]`).classList.add("is_active")
  });
});



