// EDITIONS

// input number

// Блокируем ввод символов, кроме цифр и точки
const allowedCodes = [8, 9, 27, 35, 36, 37, 38, 39, 46, 110, 188];
const pattern = /^\d+(\.?)\d{0,2}$/g;
document.getElementById('editionsNumber2').oninput = function(e) {
  const value = this.value;
  if( !(value.replace(/\s/g, '').match(pattern) || allowedCodes.some(code => code === e.keyCode)) ) this.value = value.slice(0, -1);
}

// Ожидаем нажатие и отпускание кнопки
var val_input = 0;
document.getElementById('editionsNumber2').onkeyup = function() {
  val_input = document.getElementById('editionsNumber2').value;  
  val_input = val_input.replace(/\s/g, '');
  
  // Если строка содержит точку, берем точку и все что после нее
  if (!val_input) return;  
  var hasDotInNumber = val_input.includes('.');
  if (hasDotInNumber)
    var stringAfterDot = val_input.toString().split('.').pop();
  
  val_input = Number.parseFloat(val_input);
  if (isNaN(val_input)) return;  
    
  document.getElementById('editionsNumber2').value = parseInt(val_input).toLocaleString('ru').replace(',', '.') + (hasDotInNumber ? '.' + stringAfterDot : '');  
}


// YANDEX MAP
ymaps.ready(init);
function init(){

  var myMap = new ymaps.Map("contactsMap", {
    center: [55.759856, 37.600357],
    zoom: 14
  });

  var myPlacemark = new ymaps.Placemark([55.758468, 37.601088], {}, {
    iconLayout: 'default#image',
    iconImageHref: 'img/contacts/placemark.png',
    iconImageSize: [20, 20],
    iconImageOffset: [0, -15]
  });

  myMap.geoObjects.add(myPlacemark);

  myMap.controls.remove('zoomControl');
  myMap.controls.remove('geolocationControl');
  myMap.controls.remove('fullscreenControl');
  myMap.controls.remove('routeButtonControl');
  myMap.controls.remove('trafficControl');
  myMap.controls.remove('typeSelector');
  myMap.controls.remove('typeSelector');
  myMap.controls.remove('rulerControl');
  myMap.controls.remove('searchControl');

  var zoomControl = new ymaps.control.ZoomControl({
    options: {
      position: {
        top: 200,
        left: 'auto',
        right: 10
      }
    }
  });

  myMap.controls.add(zoomControl);


  var geolocationControl = new ymaps.control.GeolocationControl({
    options: {
      position: {
        bottom: 250,
        left: 'auto',
        right: 10
      }
    }
  });

  myMap.controls.add(geolocationControl);
};